.header {
	position: relative;
	background: white;
	padding: 3px 0;
	@include transition(all .2s ease);
	@include min-responsive($screen-sm-min) {
		padding: 0;
	}
	&--fixed {
		position: fixed;
		z-index: 9;
		top: 0;
		width: 100%;
	}

	&__menu {
		padding: 7px 0px;

		@include min-responsive($screen-sm-min) {
			padding: 10px 0px;
		}

		.search__bar {
			position: relative;
			margin: 10px 0;
			margin-right: 10px;
			&.expand {
				border: 1px solid #ddd;
				>.search__form {
					width: 230px;
					height: 35px;
				}
				.search__submit {
					@include min-responsive($screen-sm-min) {
						width: 24%;
					}
	
					@include min-responsive($screen-lg-min) {
						width: 17%;
					}
				}
	
				.search__icon {
					&:hover {
						background: none;
					}
				}
			}
		}

		.search__form {
			width: 35px;
			height: 35px;
		}
		
	}

	&__divider {
		position: relative;
		padding-top: 60px;

		@include min-responsive($screen-sm-min) {
			padding-top: 75px;
		}
	}

	&__item {
		position: relative;
		display: inline-block;
		.desktop-hide{
			a{
				width: 35px;
				height: 35px;
				margin-top: 3px;
			}
		}
	}

	&__link {
		color: #000;
		text-transform: uppercase;
		font-size: 11px;
		line-height: 1;
		padding: 18px 10px;
		display: inline-block;
		border: 1px solid transparent;
		margin: 0;
		font-weight: 700;

		&--login {
			color: #000;
			border: 1px solid #fff;
		}

		// &:hover {
		// 	color: #000;
		// 	//border-radius: 8px;
		// 	//border: 1px solid #fff;
		// 	background: darken($color_theme, 10%);
		// }
	}

	&__icon {
		position: relative;
		display: block;
		width: 40px;
		height: 40px;

		//border-radius: 100%;
		@include min-responsive($screen-sm-min) {
			width: 50px;
			height: 50px;
		}

		&:hover {
			background: #f0f0f0;

			@include min-responsive($screen-sm-min) {
				background: transparent;
			}
		}

		&--logged {

			//border-radius: 0;
			//margin: 0 5px;
			@include min-responsive($screen-sm-min) {
				border: 1px solid #fff;
				margin: 0 0 0 10px;
			}
		}
	}
}