.badge {
	background: #FF6969;
	font-weight: 300;
	color: #fff;
	font-size: 11px;
	line-height: 1;
	padding: 2.5px 0;
	border-radius: 100%;
	width: 16px;
	height: 16px;
	text-align: center;
	box-shadow: 0 2px 20px 0 rgba(0, 0, 0, 0.1);
	&__notif {
		position: absolute;
		pointer-events: none;
		bottom: 6px;
		left: 12px;
		z-index: 1;
		opacity: 1;
	}
	&__inline {
		position: absolute;
		left: 6px;
		top: 13px;
		z-index: 1;
		opacity: 1;
		@include min-responsive($screen-sm-min) {
			top: 9px;
		}
	}
	&--hide {
		opacity: 0;
	}
}
