.nav {
	position: relative;
	max-width: 768px;
	overflow: scroll;
	margin: 0 auto;
	margin-bottom: 20px;

	@include min-responsive($screen-sm-min) {
		max-width: none;
		overflow: hidden;
	}

	&__bar {
		position: fixed;
		top: 60px;
		right: 0;
		z-index: 9;
		background: #fff;
		width: 100%;
		height: 100%;
		padding: 20px 10px 70px;

		@include max-responsive($screen-sm-min-1) {
			overflow-y: scroll;
			white-space: nowrap;
			backface-visibility: hidden;
			-webkit-overflow-scrolling: touch;
		}

		@include min-responsive($screen-sm-min) {
			height: auto;
			padding: 0;
			position: relative;
			top: 0;
			height: 60px;
		}

		&.is_stuck {
			box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1);
			height: 55px;

			.nav {
				margin-bottom: 0px;
			}
		}
	}

	&--hide {
		@include max-responsive($screen-sm-min-1) {
			visibility: hidden;
			-ms-transform: translate3d(100%, 0, 0);
			-webkit-transform: translate3d(100%, 0, 0);
			transform: translate3d(100%, 0, 0);
		}

		@include min-responsive($screen-sm-min) {
			display: block;
		}
	}

	&__wrap {
		position: relative;
		padding: 0;

		@include min-responsive($screen-sm-min) {
			display: block;
			position: relative;
			margin: 0 11px;
			max-height: 40px;
			overflow: hidden;
		}
		&:hover{
			.slick-arrow{
				opacity: 1;
			}
		}
		.slick {
			&-arrow {
				position: absolute;
				z-index: 2;
				width: 35px;
				height: 100%;
				top: 9px;
				opacity: 0;
			}
		}
		
		.events {
			&__prev {
				left: 0;
				padding-right: 10px;
				background: linear-gradient(90deg, #fff, transparent);
				span{
				background-size: 100%;
			}
			}
			
			&__next {
				right: 0;
				padding-left: 10px;
				background: linear-gradient(90deg, transparent, #fff);
				span{
				background-size: 100%;
			}
			}
		}
	}

	&__item {
		@include min-responsive($screen-sm-min) {
			display: inline-block;
			transition: .1s ease-in-out;
			width: auto;
			&:hover{
				text-shadow: 0px 0px 0px #000000;
			}
		}
	}

	&__link {
		display: block;
		text-align: right;
		font-weight: 300;
		padding: 10px;
		font-size: 16px;
		color: #4A4949;
		text-transform: uppercase;
		border-bottom: 3px solid transparent;
		white-space: pre-wrap;
		@include min-responsive($screen-sm-min) {
			font-size: 14px;
			font-weight: 500;
			padding: 20px 0;
			margin: 0 16px 0 32px;
			text-transform: inherit;
			text-align: center;
		}

		&--active,
		&:hover {
			font-weight: 700;
			color: $color_theme;

			@include min-responsive($screen-sm-min) {
				color: inherit;
				font-weight: 500;
				// border-bottom: 3px solid $color_theme;
			}
		}
	}
}