.cart {
	position: relative;
	margin: 0;
	
	&__action {
		position: absolute;
		z-index: 1;
		top: -10px;
		right: -10px;
		@include min-responsive($screen-sm-min) {
			top: 10px;
			right: 10px;
		}
	}
	&__remove {
		width: 30px;
		height: 30px;
		display: block;
		@include linier_gradient_2($color_gradl, $color_gradd, 180deg);
		border-radius: 100%;
		padding: 4px;
		&:hover {
			background: darken($color_theme, 12%);
			box-shadow: 0 2px 2px rgba(0,0,0,.1);
		}
	}
	&__body {
		position: relative;
        display: flex;
	}
	&__content {
		position: relative;
		padding: 0;
		margin: 0 0 40px;
		@include min-responsive($screen-sm-min) {
			margin: 0;
		}
	}
	&__item {
		position: relative;
		margin: 0 0 20px;
		border-radius: 10px;
		border: 1px solid #707070;
		box-shadow: 0 2px 5px rgba(0,0,0,.1);
		padding: 10px;
		&:last-child {
			margin: 0;
		}
		@include min-responsive($screen-sm-min) {
			padding: 20px;
        }
        .icon{
            filter: invert(1);
        }
	}
	&__img {
        flex: 0 0 50%;
        @include min-responsive($screen-sm-min) {
            flex: 0 0 30%;
		}
        &__content{
            position: relative;
            img{
                border-radius: 30px;
            }
        }
        &__type {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			padding: 7px 20px;
			border-radius: 10px 10px 0 0;
			background: $color_border;
			color: #000;
            text-transform: uppercase;
            padding: 3px 15px;
            font-size: 10px;
		}
    }
    &__box {
		padding: 10px;
        margin: 0 0 40px;
        flex: 0 0 50%;
		@include min-responsive($screen-sm-min) {
            flex: 0 0 70%;
			margin: 0;
			padding: 20px 20px;
		}
	}
    &__category {
		font-family: $font_vik;
		font-size: 14px;
		margin: 5px 0;
		@include max-responsive($screen-sm-min-1) {
			@include title_overflow(auto, 55px);
		}
		@include min-responsive($screen-sm-min) {
			font-size: 23px;
			margin: 5px 0 10px;
		}
	}
	&__title {
		font-family: $font_vik;
		font-size: 20px;
		margin: 5px 0;
		@include max-responsive($screen-sm-min-1) {
			@include title_overflow(auto, 55px);
		}
		@include min-responsive($screen-sm-min) {
			font-size: 30px;
			margin: 5px 0 10px;
		}
	}
	
	&__lead {
		font-size: 12px;
        padding: 2px 0;
        max-height: 79px;
        overflow: hidden;
        @include min-responsive($screen-sm-min) {
			font-size: 16px;
		}
	}
	&__author {
		position: relative;
		color: #D1CDCD;
		text-transform: uppercase;
		font-size: 12px;
		margin: 2px 0;
		padding: 0;
	}
	&__place {
		position: relative;
		color: #2a2a2a;
		font-size: 10px;
		margin: 2px 0;
		padding: 0;
		@include min-responsive($screen-sm-min) {
			font-size: 12px;
		}
	}
	&__time {
		position: relative;
		color: #2a2a2a;
		font-weight: 500;
		font-size: 12px;
		margin: 2px 0;
		padding: 0;
	}
	&__price {
		font-size: 14px;
		font-weight: 500;
		color: $color_theme;
		padding: 5px 0 15px;
	}
	&__total {
		padding: 20px 0 0;
		text-align: right;
		span {
			display: inline-block;
			padding: 0;
			font-size: 18px;
			font-weight: 700;
		}
	}
	&__rincian {
		@include display-flex();
		padding: 10px 0;
		span {
			display: block;
			padding: 3px 0 0;
			width: 45%;
			&:last-child {
				width: 55%;
				padding: 0;
				//font-size: 18px;
				//font-weight: 700;
				text-align: right;
			}
			&.disc {
				color: #f00;
			}
		}
	}
	&__grandtotal {
		@include display-flex();
		padding: 20px 0 10px;
		span {
			display: block;
			padding: 3px 0 0;
			width: 45%;
			&:last-child {
				width: 55%;
				padding: 0;
				font-size: 18px;
				font-weight: 700;
				text-align: right;
			}
		}
	}
}

.item {
	&__quantity {
		position: relative;
        padding: 5px 0 0;
        display: flex;
        flex-direction: column;
        @include min-responsive($screen-md){
            align-items: center
        }
		span {
			display: inline-block;
			padding: 12px 5px;
			font-size: 11px;
			line-height: 1;
			vertical-align: middle;
		}
		span.error {
			display: block;
			padding: 2px 0 1px;
			&:empty {
				padding: 0;
			}
		}
    }
    &__counter{
        @include min-responsive($screen-md){
            margin-left: 2.5rem;
        }
    }
	&__btn {
		height: 35px;
		box-shadow: none;
		padding: 0 9px;
		font-size: 18px;
		line-height: 1;
		font-weight: 500;
		border-radius: 0;
		vertical-align: middle;
		display: inline-block;
		float: left;
		color: #000;
		&:disabled {
			color: darken(#f4f4f4, 10%);
		}
	}
	&__count {
		height: 35px!important;
		width: 45px;
		box-shadow: none;
		border-right: 0px;
		border-left: 0px;
		border-radius: 0;
		padding: 0;
		color: #000;
		text-align: center;
		vertical-align: middle;
		display: inline-block;
		float: left;
		&:hover {
			//box-shadow: none;
			border-left: 0px;
			border-right: 0px;
		}
	}
}
