.subs {
	position: relative;

	&__list {
		position: relative;
	}

	&__item {
		padding: 10px;
		border-radius: 10px;
		border: 1px solid #707070;
		margin: 20px 0;
		background: #fff;
		border-radius: 10px;
		@include min-responsive($screen-sm-min) {
			padding: 20px;
		}

		&:first-child {
			margin: 0;
		}
	}

	&__body {
		position: relative;
	}

	&__warning {
		position: absolute;
		z-index: 1;
		line-height: 1;
		font-weight: 700;
		color: $color_error;
		text-align: center;
		background: #fff;
		text-align: center;
		border: 1px solid $color_error;
		border-radius: 100%;
		top: -20px;
		right: -20px;
		font-size: 20px;
		width: 30px;
		height: 30px;
		padding: 5px 5px;

		@include min-responsive($screen-sm-min) {
			top: 0;
			right: 0;
			font-size: 30px;
			width: 40px;
			height: 40px;
			padding: 5px 5px;
		}
	}

	&__img {
		margin: 0 0 10px 10px;
		float: right;
		width: 50px;

		img {
			border-radius: 10px;
		}

		//display: none;
		@include min-responsive($screen-sm-min) {
			margin: 0 20px 0 0;
			float: left;
			width: 170px;
			display: block;
		}
	}

	&__title {
		font-family: $font_vik;
		font-size: 18px;
		margin: 0;

		@include min-responsive($screen-sm-min) {
			font-size: 30px;
			margin: 5px 0 10px;
		}
	}

	&__price {
		font-family: $font_vik;
		font-size: 18px;
		margin: 0;

		@include min-responsive($screen-sm-min) {
			font-size: 30px;
			margin: 5px 0 10px;
		}
	}

	&__category {
		color: #D1CDCD;
		font-size: 14px;
		text-transform: uppercase;
		margin: 0 0 2px;
		font-weight: 500;
		padding: 0;
	}

	&__valid {
		&__date {
			font-size: 12px;
			padding: 2px 0;
			font-weight: 500;
		}

		&__msg {
			padding: 15px 0;
			font-size: 12px;
			color: $color_error;
		}
	}

	&__action {
		position: relative;
		padding: 10px 0 0;

		.button {
			display: inline-block;
			width: 120px;
			margin-right: 5px;
		}
	}

	// type 
	&__type {
		padding: 0 0 10px;
		font-weight: 500;
		font-size: 10px;
		color: $color_border;
		text-transform: capitalize;
		@include min-responsive($screen-sm-min) {
			font-size: 14px;
			margin: 0;
		}
	}

	// history 
	&__history {
		&__date {
			padding: 0 0 10px;
			font-weight: 500;
			font-size: 15px;
			color: $color_border;

			@include min-responsive($screen-sm-min) {
				font-size: 22px;
				margin: 0;
			}
		}
	}

	&__list.history {
		.subs__img {
			width: 100px;

			@include min-responsive($screen-sm-min) {
				width: 200px;
			}
		}

		.subs__title {
			font-size: 18px;

			@include min-responsive($screen-sm-min) {
				font-size: 30px;
			}
		}
	}
}