.search {
	&__bar {
		position: relative;
		display: block;
		background: transparent;
		border-radius: 8px;
		overflow: hidden;
		transition: all .2s ease-in-out;
	}

	&__icon {
		position: absolute;
		top: 3px;
		left: 3px;
		width: 30px;
		height: 30px;
		cursor: pointer;
	}

	&__input {
		box-shadow: none;
		background: transparent;
		float: left;
		border: 1px solid $color_grey;
		border-radius: 8px 0px 0px 8px;
		padding: 6px 12px 6px 40px;
		height: 35px;
		@include min-responsive($screen-sm-min) {
			width: 0;
			padding: 0;
			height: 0;
			display: none;
		}

		@include min-responsive($screen-lg-min) {
			width: 0;
			padding: 0;
			height: 0;
		}
		@include max-responsive($screen-lg-min){
			width: 79%;
		}
		&::placeholder {
			color: #c4c4c4;
			text-transform: uppercase;
		}
		
	}
	
	&__submit {
		width: 17%;
		height: 35px;
		float: left;
		font-size: 12px;
		border-radius: 0;
		border: transparent;
		color: #fff;
		text-transform: uppercase;
		font-weight: 300;
		@include linier_gradient_2($color_gradl, $color_gradd, 180deg);
		
		@include min-responsive($screen-sm-min) {
			width: 0;
			padding: 0;
			height: 35px;
		}
		
		@include min-responsive($screen-lg-min) {
			width: 0;
			padding: 0;
			height: 35px;
		}
		@include max-responsive($screen-lg-min){
			width: 0%;
			height: 35px;
		}
		@include max-responsive($screen-sm-min){
			width: 21%;
			height: 35px;
		}
	}
	
	&__result {
		position: relative;
		padding: 25px 0;
		font-size: 16px;

		@include min-responsive($screen-sm-min) {
			font-size: 18px;
		}
	}

	&__bar {
		&.expand {
			border: 1px solid #ddd;
			.search__submit {
				@include min-responsive($screen-sm-min) {
					position: absolute;
					height: 35px;
					width: 25%;
    				right: 0px;
				}
			}

			.search__icon {
				&:hover {
					background: none;
				}
			}

			.search__input {
				width: 91%;
				height: 35px;
				padding: 0 35px;
				@include min-responsive($screen-sm-min){
					display: block;
				}
			}
		}
	}
}