.banner {
	position: relative;
	margin-bottom: 20px;
	@include max-responsive($screen-sm-min){
		margin-top: 20px;
	}
	&__item {
		position: relative;
		width: 100%;
		text-align: center;
	}
	&__img {
		display: block;
		img {
			width: 100%;
		}
		span {
			margin: 0 auto;
			display: block;
			background-position: center center;
			background-repeat: no-repeat;
			background-size: cover;
			width: 100%;
			height: 94px;
			@include min-responsive($screen-sm-min) {
				width: 100%;
				height: 94px;
			}
		}
	}
	&__link {
		display: block;
	}
	.slick-dots {
		bottom: 15px;
		z-index: 1;
		padding-right: 20px;
		text-align: right;
	}
	.slick-dots li {
		margin: 0 5px;
		width: 10px;
		height: 10px;
	}
	.slick-dots li button {
		background: transparent;
		border-radius: 0;
		border: 1px solid #fff;
		width: 10px;
		height: 10px;
		padding: 0;
		border-radius: 100%;
	}
	.slick-dots li button:before {
		content: '';
		width: 10px;
		height: 10px;
	}
	.slick-dots li.slick-active button {
		background: #fff;
	}
	.slick-slide{
		width: 315px;
		margin-right: 10px;
		@include min-responsive($screen-sm-min) {
			width: 280px;
			margin-right: 0px;
		}
		@include max-responsive($screen-5s) {
			width: 280px;
		}
	}
	img{
		width: 100%;
	}
	&__dots{
		position: absolute;
		bottom: 10px;
		right: 5px;
		z-index: 1;
		color: #fff;
		font-size: 12px;
	}
	&__count{
		background: rgba(0,0,0,.75);
		margin-right: 2px;
		padding: 5px 10px;
		border-radius: 10px 0px 0px 10px;
		&Text{
			background: rgba(0,0,0,.75);
			padding: 5px 10px;
			border-radius: 0 10px 10px 0;
		}
	}
	a{
		&:hover{
			color: inherit;
		}
	}
}
