.display-table {
	display: table;
	@include display_full();
	position: relative;
	z-index: 1;
}
.display-center {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	@include display_full();
}
.text-left {
	text-align: left;
}
.text-right {
	text-align: right;
}
.text-center {
	text-align: center;
}
.divider {
	padding: 2px 0;
	background: #f0f0f0;
	margin: 40px 0;
}
.pull-right {
	float: right;
}
.pull-left {
	float: left;
}

.mb10{
	margin-bottom: 100px;
}

// override
.form__attribute.text-right {
	@include max-responsive($screen-sm-min-1) {
		text-align: left;
	}
}
.desktop-hide {
	@include min-responsive($screen-sm-min) {
		display: none;
	}
}
.mobile-hide {
	@include max-responsive($screen-sm-min-1) {
		display: none;
	}
}
.academy__list.search {
	.academy__item:first-child {
		width: 100%;
		@include min-responsive($screen-sm-min) {
			width: 33.333333%;
		}
		@include min-responsive($screen-lg-min) {
			width: 25%;
		}
	}
}
.load__more {
	padding: 20px 0;
}
.login__wrap .error.form__group span {
	color: #fff;
}
body.scroll {
	.login__box {
		top: 70px
	}
	.board__box {
		top: 70px;
	}
	.header {
		top: -25px;
	}
	.nav__bar {
		top: 70px;
	}
