.paging {
	position: relative;
	&__wrap {
		position: relative;
		padding: 10px 0;
	}
	&__teaser {
		padding: 5px 0;
		@include font-pack(false, 16px, 600, false, false);
	}
	&__item {
		display: inline-block;
		vertical-align: middle;
		margin-bottom: 5px;
	}
	&__link {
		padding: 5px 9px;
		display: block;
		line-height: 1;
		border-radius: 20px;
		// border-radius: 5px;
		color: #4a4a4a!important;
		@include font-pack(false, 18px, 400, false, false);
		&--prev,
		&--next {
			color: #666!important;
			@include font-pack(false, 12px, 300, false, false);
		}
		&--show {
			text-align: center;
			padding: 6px 10px;
			margin: 1px 4px;
			background: lighten(#2a2a2a, 10%);
			color: #fff!important;
			@include font-pack(false, 14px, 400, false, false);
		}
		&--active,
		&:hover {
			background: #2a2a2a;
			color: #fff!important;
		}
	}
	&--article {
		padding: 7px 10px 4px;
		background: #fafafa;
	}
}
