.logo {
	position: relative;
	margin-left: 0px;
	@include min-responsive($screen-sm-min) {
		margin-left: 0;
	}
	&__link {
		display: inline-block;
	}
	&__img {
		position: relative;
		background-position: 0px;
		background-repeat: no-repeat;
		background-size: 185px;
		height: 55px;
		width: 185px;
		@include min-responsive($screen-sm-min) {
			background-position: left 8px;
			background-size: 225px;
			width: 235px;
			height: 70px;
		}
		@include max-responsive($screen-5s){
			background-size: 165px;
		height: 55px;
		width: 165px;
		}
	}
}