.board {
	position: relative;
	&__box {
		position: fixed;
		z-index: 10;
		backface-visibility: hidden;
		@include linier_gradient_2($color_gradl, $color_gradd, 180deg);
		width: 100%;
		height: 100%;
		top: 55px;
		right: 0;
		padding: 20px 15px 70px;
		@include transition(all .2s ease);
		@include min-responsive($screen-sm-min) {
			width: 240px;
			height: auto;
			top: 85px;
			right: calc((100% - 760px)/2);
			padding: 0;
			box-shadow: 0 2px 5px rgba(0,0,0,.1);
			&:after {
				content: '';
				position: absolute;
				z-index: 1;
				top: -16px;
				right: 27px;
				width: 0;
				height: 0;
				border-left: 8px solid transparent;
				border-right: 8px solid transparent;
				border-top: 8px solid transparent;
				border-bottom: 8px solid $color_theme;
			}
		}
		@include min-responsive($screen-lg-min) {
			right: calc((100% - 1100px)/2);
		}
		@include max-responsive($screen-sm-min-1) {
			overflow-y: scroll;
			white-space: nowrap;
			-webkit-overflow-scrolling: touch;
		}
		.form__submit {
			padding: 0 0 20px;
		}
	}
	&--hide {
		@include max-responsive($screen-sm-min-1) {
			visibility: hidden;
			-ms-transform: translate3d(100%, 0, 0);
			-webkit-transform: translate3d(100%, 0, 0);
			transform: translate3d(100%, 0, 0);
		}
		@include min-responsive($screen-sm-min) {
			visibility: hidden;
			opacity: 0;
			-ms-transform: translate3d(0, -5px, 0);
			-webkit-transform: translate3d(0, -5px, 0);
			transform: translate3d(0, -5px, 0);
		}
	}
	&__wrap {
		position: relative;
		@include min-responsive($screen-sm-min) {
			padding: 10px;
		}
	}
	&__item {
		position: relative;
	}
	&__link {
		display: block;
		text-align: right;
		font-weight: 300;
		font-size: 16px;
		line-height: 1;
		padding: 15px 10px;
		color: #fff;
		text-transform: uppercase;
		border-bottom: 1px solid #fff;
		&--active,
		&:hover {
            color: #fff;
            font-weight: 700;
		}
		@include min-responsive($screen-sm-min) {
			font-size: 14px;
			padding: 10px;
			border-bottom: 0px solid #fff;
		}
	}
}
