.hero {
    display: block;
    position: relative;
    &__bg {
        position: relative;
        background-size: cover;
        background-position: center;

        @include max-responsive($screen-sm-min) {
            img {
                width: 100%;
            }
        }

        &--home {
            border-radius: 50px 50px 0 0px;

            &::before {
                border-radius: 50px 50px 0 0px;
                height: 100%;
            }
        }
    }

    &__box {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: center;
        color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0 25px 10px;

        @include min-responsive($screen-sm-min) {
            padding: 0rem 10rem;
        }
    }

    &__title {
        font-size: 30px;
        font-family: $font_title;
        
        letter-spacing: 2px;
        line-height: 1;
        
        @include min-responsive($screen-md) {
            font-size: 42px;
            letter-spacing: 5px;
        }
        @include max-responsive($screen-lg) {
            font-size: 36px;
        }
        @include max-responsive($screen-sm-min) {
            font-size: 24px;
        }
    }

    &__sub {
        font-size: 16px;
        font-family: $font_body;

        @include min-responsive($screen-md) {
            font-size: 20px;
        }
        @include max-responsive($screen-lg) {
            font-size: 16px;
        }
        @include max-responsive($screen-sm-min) {
            font-size: 12px;
        }
    }

    &__text {
        margin: 5px 0;

        @include min-responsive($screen-sm-min) {
            margin: 10px 0;
        }
    }

    &__submit {
        padding: 10px 0px;
        padding-top: 0px;
        text-align: center;

        @include min-responsive($screen-sm-min) {
            padding: 0px 10px;
            margin: 10px 0;
        }
    }

    &__inside {
        @include max-responsive($screen-sm-min) {
            padding: 0 5px;
        }
    }

    &__alternate {
        .hero__box {
            position: relative;
            display: block;
            padding: 0 8px;
            text-align: left;
            @include min-responsive($screen-sm-min){
                padding: 0 15px;
            }
        }

        .hero__text {
            @include min-responsive($screen-md) {
                margin: 14px 0;
            }
            @include max-responsive($screen-md) {
                margin: 20px 0;
            }
            @include max-responsive($screen-sm-min) {
                margin: 14px 0;
            }
        }


        .hero__bg {
            padding: 4rem 0 0;

            @include min-responsive($screen-sm-min) {
                padding: 4rem 0 0;
            }
        }

        &--retail {

            .hero__inside {
                &:first-child {
                    .hero__box {
                        background: rgba(0, 0, 0, .75);
                        border-radius: 0px 0px 0 30px;
                        @include min-responsive($screen-sm-min) {
                            border-radius: 0px 0px 0 0px;
                        }
                    }
                }
            }

            .hero__bg {
                @include min-responsive($screen-sm-min) {
                    padding: 4rem 0 0;
                }

                &--big {
                    border-radius: 30px 0px 0 30px;

                    @include min-responsive($screen-sm-min) {
                        border-radius: 50px 0px 0 0px;
                    }

                    &::before {
                        height: 55%;
                    }
                }

                &--small {
                    border-radius: 0px 30px 30px 0px;

                    @include min-responsive($screen-sm-min) {
                        border-radius: 0px 50px 0 0px;
                    }

                    &::before {
                        border-radius: 0px 50px 0 0px;
                        height: 100%;
                    }
                }
            }
        }

        &--epaper {

            .hero__inside {
                &:last-child {
                    .hero__box {
                        background: rgba(0, 0, 0, .75);
                        border-radius: 0px 0px 30px 0px;
                    }
                }
            }

            .hero__bg {

                &--big {
                    border-radius: 0px 30px 30px 0px;

                    @include min-responsive($screen-sm-min) {
                        border-radius: 0px 50px 0 0px;
                    }
                }

                &--small {
                    border-radius: 30px 0px 0px 30px;

                    @include min-responsive($screen-sm-min) {
                        border-radius: 50px 0px 0 0px;
                    }
                }
            }
        }
    }
    &__wave{
        img{
            width: 100%;
        }
    }
}