.static{
    color: #000;
    &__wrapper{
        padding: 0px 0 165px;
    }
    &__content{
        padding: 20px 0;
        ul{
            list-style: decimal;
            padding-left: 1.5em;
            padding-right: 1.5em;
            font-size: 14px;
            @include min-responsive($screen-sm-min){
                font-size: 16px;
                padding-left: 1em; 
            }
            li{
                margin: 5px 0;
                line-height: 1.5;
            }
            ul{
                list-style: lower-alpha;
            } 
        }
        table{
            width: 100%;
            @include min-responsive($screen-sm-min){
                width: 40%;
            }
            tr{
                &:last-child{
                    td{
                        &:last-child{
                            border-top: 1px solid #000;
                        }
                    }
                }
            }
        }
        &--b{
            label{
                display: block;
                margin-top: 20px;
                font-size: 16px;
                font-weight: 700;
            }
            ul{
                list-style: decimal;
                padding-left: 1.5em;
                padding-right: 1.5em;
                font-size: 14px;
                @include min-responsive($screen-sm-min){
                    font-size: 16px;
                    padding-left: 2em; 
                }
                li{
                    margin: 5px 0;
                    line-height: 1.5;
                }
                ul{
                    list-style: circle;
                } 
            }
        }
    }
    p{
        font-size: 16px;
    }
    abbr{
        display: block;
        margin: 50px 0;
    }
    
}