.step {
	position: relative;
	@include max-responsive($screen-sm-min-1) {
		overflow-y: hidden;
		white-space: nowrap;
		margin: 0 -10px;
		padding: 10px 10px;
	}
	&__bar {
		position: relative;
		background: #E9E9E9;
		margin: 0 0 20px;
	}
	&__wrap {
		position: relative;
		@include min-responsive($screen-sm-min) {
			@include display-flex();
			//flex-wrap: wrap;
			justify-content: center;
		}
	}
	&__item {
		width: 100px;
		margin: 0;
		display: inline-block;
		position: relative;
		vertical-align: top;
		padding: 10px 0;
		@include min-responsive($screen-sm-min) {
			width: 175px;
			padding: 15px 0px;
		}
		@include min-responsive($screen-lg-min) {
			width: 200px;
		}
		&.active {
			&::before{
				content: '';
				position: absolute;
				bottom: 0;
				left: 20px;
    			width: calc(100% - 40px);
				height: 3px;
				background: $color_theme;
            }
            &::after{
                height: calc(100% + 4px);
            }
		}
	}
	&__title {
		text-align: center;
		font-size: 12px;
		letter-spacing: .2px;
		color: #000;
		backface-visibility: hidden;
		-webkit-overflow-scrolling: touch;
		white-space: normal;
		padding: 0 10px;
		@include display-flex();
		align-items: center;
		justify-content: center;
		border-right: 2px solid #000;
		min-height: 30px;
		@include min-responsive($screen-sm-min) {
			line-height: 1;
		}
	}
	&__link {
		display: block;
	}
}