.product {
	position: relative;

	&__category {
		margin: 50px 0 20px;
		text-align: center;
	}

	&__share {
		text-align: center;
		margin-bottom: 20px;
		@include min-responsive($screen-lg){
			text-align: right;
		}
	}
	&__title{
		.title{
			@include min-responsive($screen-md){
				text-align: left;
			}
		}
	}

	&__detail {
		margin-bottom: 75px;
	}

	&__img {
		&__wrapper{
			position: relative;
		}
		&__type {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			padding: 7px 20px;
			border-radius: 10px 10px 0 0;
			background: $color_border;
			color: #000;
		}

		&__item {
			img {
				border-radius: 40px;
				margin: 0 auto;
			}
		}
	}

	&__thumb {
		margin-top: 20px;

		&__item {
			margin-right: 24px;
			cursor: pointer;

			&:last-child {
				margin: 0;
			}
		}

		img {
			border-radius: 10px;
			margin: 0 10px;
		}
	}

	&__term {
		padding: 10px 15px;
		border: 1px solid $color_border;
		border-radius: 30px;

		&Text {
			color: $color_border;
			font-size: 13px;
			line-height: 1.5;

			a {
				color: $color_theme;
			}
		}
	}

	&__price {
		padding: 5px 10px;
		background: #fff;
		font-weight: 700;
		font-size: 18px;

		@include min-responsive($screen-md) {
			font-size: 20px;
		}

		&__item {
			border: 1px solid #000;
			border-radius: 30px;
			margin-bottom: 20px;
			padding: 15px;
		}

		&__top {
			display: flex;
			align-items: center;
			justify-content: space-between;
			@include max-responsive($screen-sm-min) {
				flex-direction: column;
			}
		}

		&__bottom {
			margin: 10px 0;
			padding-left: 55px;

			@include max-responsive($screen-md) {
				padding-left: 0;
			}
		}

		&__discount {
			position: relative;
			font-size: 12px;
			display: inline-block;
			padding: 0 10px;
			margin: 0;

			&::before {
				content: '';
				position: absolute;
				top: 50%;
				left: 0;
				width: 100%;
				height: 1px;
				background: $color_error;
			}
		}

		&__actual {
			font-family: $font_vik;
			font-size: 40px;

			@include max-responsive($screen-sm-min) {
				font-size: 26px;
				text-align: center;
			}
		}

		&__note {
			font-weight: 700;
			font-size: 12px;
			color: $color_error;
			margin: 0;
		}

		&__spec {
			flex: 0 0 60%;
			padding: 0 5px;
		}

		input {
			flex: 0 0 10%;
			text-align: center;
		}

	}

	&__time {
		flex: 0 0 30%;

		&__month {
			font-weight: 700;
			font-size: 30px;
			color: $color_border;

			@include max-responsive($screen-md) {
				font-size: 18px;
			}
			@include max-responsive($screen-5s) {
				font-size: 17px;
			}
		}

		&__edition {
			font-weight: 700;
			font-size: 14px;
			color: $color_border;
			margin: 0;
		}
	}
}