.card {
    margin: 20px 0 0;

    &__wrapper {
        margin-top: 10px;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;

        @include max-responsive($screen-sm-min) {
            flex-direction: row;
            flex-wrap: wrap;
        }
    }

    &__category {
        position: absolute;
        left: 50%;
        top: -15px;
        transform: translateX(-50%);
        background: #fff;
        padding: 10px 15px;
        border: 1px solid $color_submit;
        z-index: 1;
        max-height: 35px;
        overflow: hidden;
        font-size: 12px;

        @include max-responsive ($screen-sm-min) {
            top: -10px;
            padding: 5px 10px;
        }
    }

    &__item {
        position: relative;
        margin: 15px;
        flex: 0 0 calc(25% - 31px);

        &:hover {
            img {
                transform: scale(1.2)
            }
        }

        @include max-responsive($screen-sm-min) {
            margin: 15px 10px 30px;
            flex: 0 0 calc(33% - 20px);

            &:hover {
                img {
                    transform: scale(1)
                }
            }
        }

        @include max-responsive($screen-phone) {
            margin: 15px 10px 30px;
            flex: 0 0 calc(50% - 20px);
        }
    }

    &__img {
        position: relative;
        overflow: hidden;
        border-radius: 30px 30px 0 0;
        height: 301px;
        @include max-responsive($screen-sm-min){
            height: auto;
        }
        img {
            width: 100%;
            border-radius: 30px;
            transition: 1s ease-in-out;
            transform: scale(1);
            height: 100%;
            object-fit: cover;
        }

        &:before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 50px;
            background: #000;
            z-index: -1;
        }
    }

    &__box {
        position: relative;
        padding: 15px 20px;
        background: #000;
        border-radius: 0px 0px 30px 30px;
        text-align: center;
        color: #fff;
        text-align: center;
        line-height: 1.5;

        @include max-responsive ($screen-sm-min) {
            padding: 0 10px;
            padding-top: 15px;
        }

        &Type {
            position: absolute;
            left: 50%;
            top: -20px;
            background: $color_border;
            color: #000;
            padding: 2px 15px;
            transform: translateX(-50%);
            border-radius: 10px 10px 0px 0px;
            font-size: 12px;
            text-transform: uppercase;
        }

        &Title {
            font-family: $font_vik;
            letter-spacing: 2px;
            font-size: 12px;
            margin: 5px 0;
            max-height: 42px;
            overflow: hidden;

            @include max-responsive ($screen-sm-min) {
                max-height: 19px;
            }
        }

        &Lead {
            font-family: $font_body;
            font-size: 13px;
            margin: 5px 0;
            max-height: 62px;
            overflow: hidden;
        }

        &Price {
            font-family: $font_vik;
            font-size: 24px;
            margin: 5px 0;
            max-height: 62px;
            overflow: hidden;

            @include max-responsive ($screen-md) {
                margin: 0;
                font-size: 15px;
                max-height: 42px;
            }
        }

        &Footer {
            display: inline-block;
            font-family: $font_body;
            font-size: 12px;
            padding: 0px;
            margin: 5px 0;
            max-height: 62px;
            overflow: hidden;
            border-bottom: 1px solid #fff;
            font-weight: 300;
        }
    }

    &__btn {
        display: block;
        text-align: center;
        margin: 0px 0 25px;
        @include min-responsive($screen-sm-min) {
            margin: 25px 0;
        }
    }

    &Event {
        margin-bottom: 45px;

        &__title {
            margin: 25px 0;
            margin-top: 50px;
            text-align: center;
            padding: 0 10px;
            @include min-responsive($screen-sm-min) {
                text-align: right;
                margin-top: 100px;
            }
        }

        &__wrapper {
            margin-top: 10px;
        }

        &__list {
            display: flex;
            flex-wrap: wrap;

            @include max-responsive($screen-sm-min) {
                flex-direction: row;
                flex-wrap: wrap;
            }
        }

        &__item {
            position: relative;
            margin: 15px;
            flex: 0 0 calc(25% - 31px);
            max-height: 368px;
            overflow: hidden;
            border-radius: 30px;



            @include min-responsive($screen-md) {
                &:hover {
                    img {
                        transform: scale(1.2)
                    }
                }

                &:first-child {
                    flex: 0 0 calc(68% - 31px);

                    .cardEvent__flex {
                        flex-direction: row;
                        align-items: flex-end;
                    }

                    .cardEvent__boxTitle,
                    .cardEvent__boxPrice {
                        text-align: left;
                    }

                }

                &:nth-child(2) {
                    flex: 0 0 calc(32% - 31px);

                    .cardEvent__flex {
                        flex-direction: row;
                        align-items: flex-end;
                    }

                    .cardEvent__boxTitle,
                    .cardEvent__boxPrice {
                        text-align: left;
                    }

                }
            }

            @include max-responsive($screen-md) {
                margin: 15px 10px 30px;
                flex: 0 0 calc(24% - 20px);
            }

            @include max-responsive($screen-phone) {
                margin: 15px 10px 30px;
                flex: 0 0 calc(50% - 20px);
            }
        }

        &__box {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            z-index: 4;
            background: rgba(0, 0, 0, .75);
            color: #fff;
            padding: 10px 20px;

            @include max-responsive ($screen-sm-min) {
                padding: 5px 10px 25px;
            }

            &Start {
                font-size: 12px;
                margin: 5px 0;
            }

            &Title {
                font-family: $font_vik;
                letter-spacing: 2px;
                font-size: 12px;
                margin: 5px 0;
                max-height: 42px;
                overflow: hidden;
                text-align: center;

                @include max-responsive ($screen-sm-min) {
                    max-height: 30px;
                }
            }

            &Price {
                font-family: $font_vik;
                font-size: 24px;
                margin: 5px 0;
                max-height: 62px;
                overflow: hidden;
                text-align: center;

                @include max-responsive ($screen-md) {
                    margin: 0;
                    font-size: 15px;
                    max-height: 42px;
                }
            }

            &Footer {
                display: inline-block;
                font-family: $font_body;
                font-size: 12px;
                padding: 0px;
                margin: 5px 0;
                max-height: 62px;
                overflow: hidden;
                border-bottom: 1px solid #fff;
            }
        }

        &__flex {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: column;
        }

        &__img {
            position: relative;
            overflow: hidden;
            border-radius: 30px 30px 0 0;
            height: 413px;
            @include max-responsive($screen-sm-min){
                height: auto;
            }
            img {
                border-radius: 30px;
                transition: 1s ease-in-out;
                transform: scale(1);
                height: 100%;
                object-fit: cover;
            }

            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 50px;
                background: #000;
                z-index: -1;
            }
        }

    }

    &Promo {
        &__title {
            margin: 25px 0;
        }

        &__wrapper {
            margin-top: 10px;
        }

        &__list {
            display: flex;
            flex-wrap: wrap;

            @include max-responsive($screen-sm-min) {
                flex-direction: row;
                flex-wrap: wrap;
            }
        }

        &__item {
            position: relative;
            margin: 15px;
            flex: 0 0 calc(25% - 31px);
            max-height: 368px;
            overflow: hidden;
            border-radius: 30px;



            @include min-responsive($screen-md) {
                &:hover {
                    img {
                        transform: scale(1.2)
                    }
                }
            }

            @include max-responsive($screen-md) {
                margin: 15px 10px 30px;
                flex: 0 0 calc(24% - 20px);
            }

            @include max-responsive($screen-phone) {
                margin: 15px 10px 30px;
                flex: 0 0 calc(50% - 20px);
            }
        }

        &__box {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            z-index: 4;
            color: #fff;
            padding: 10px 20px;

            @include max-responsive ($screen-sm-min) {
                padding: 5px 10px 10px;
            }

            &Title {
                letter-spacing: 2px;
                font-size: 63px;
                margin: 5px 0;
                max-height: 75px;
                overflow: hidden;
                text-align: center;
                font-weight: 700;
                position: relative;
                padding-right: 10px;

                @include max-responsive ($screen-sm-min) {
                    max-height: 32px;
                    font-size: 30px;
                }

                &:after {
                    content: 'disc';
                    position: absolute;
                    top: 5px;
                    right: 3px;
                    font-size: 17px;

                    @include max-responsive ($screen-sm-min) {
                        font-size: 11px;
                    }
                }
            }
        }

        &__img {
            position: relative;
            overflow: hidden;
            border-radius: 30px 30px 0 0;

            img {
                border-radius: 30px;
                transition: 1s ease-in-out;
                transform: scale(1)
            }
        }

    }
}