.p404 {
	&__wrap {
		padding: 15vh 0;
		position: relative;
		text-align: center;
	}
	&__img {
		padding: 0 20px;
		position: relative;
	}
	&__title {
		color: $color_theme;
		line-height: 1;
		font-weight: 700;
		padding: 0 20px;
		font-size: 30px;
		@include min-responsive($screen-sm-min) {
			font-size: 50px;
		}
	}
	&__lead {
		font-size: 16px;
		padding: 20px;
		font-family: $font_title;
		text-transform: uppercase;
		letter-spacing: 2px;
		@include min-responsive($screen-sm-min) {
			font-size: 18px;
		}
	}
}
