.promo {
	&__header{
		margin: 25px 0;
		@include min-responsive($screen-sm-min) {
			margin: 50px 0;
		}
	}
	&__bar {
		padding: 15px 10px;
		background: #E9E9E9;
		margin: 0;
		@include min-responsive($screen-sm-min) {
			margin: 0;
		}
	}
	&__code {
		height: 35px!important;
		box-shadow: none;
		border-top: 0px;
		border-left: 0px;
		border-right: 0px;
		border-bottom: 2px solid $color_theme;
		background: transparent;
		border-radius: 0;
		margin-right: 5px;
		width: 70%;
		padding: 6px 0;
		color: #000;
		vertical-align: middle;
		&::placeholder {
			font-size: 11px;
			color: #C4C4C4;
		}
		&:hover,
		&:focus {
			box-shadow: none;
			border: none;
			border-bottom: 2px solid $color_theme;
		}
	}
	&__button {
		width: 25%;
		height: 35px!important;
		@include linier_gradient_2($color_gradl, $color_gradd, 180deg);
		color: #fff;
		text-transform: uppercase;
		text-align: center;
		font-size: 10px;
		border-radius: 0;
		box-shadow: none;
		padding: 2px 5px;
        vertical-align: middle;
        border-radius: 10px;
		&:hover {
			border: 2px solid $color_theme;
		}
	}
	&__box{
		position: relative;
		display: block;
		margin: 30px 0;
		@include min-responsive($screen-sm-min) {
			margin: 30px 0 100px;
		}
		
		h2{
			font-family: $font-vik;
			font-size: 23px;
			@include min-responsive($screen-sm-min) {
				font-size: 30px;
		}
		}
		h3{
			font-family: $font-vik;
			font-size: 23px;
			@include min-responsive($screen-sm-min) {
				font-size: 30px;
		}
		}
		p{
			font-size: 20px;
		}
		ul{
			list-style-type: decimal;
			padding-left: 1em;
			li{
				margin: 5px 0;
			}
		}
	}
	&__bg{
		background: $color_promo;
		border-radius: 20px 20px 0px 0px;
		padding-top: 20px;
	}
}
