.error {
	display: block;
	padding: 2px 0 1px;
	font-size: 11px;
	color: $color_error;
	&.form__group {
		display: block;
		padding: 0;
		color: inherit;
		font-size: inherit;
		select,
		textarea,
		input[type="password"],
		input[type="text"] {
			border: 1px solid $color_error;
		}
		span {
			display: inline-block;
			font-size: 11px;
			padding: 5px 5px 0;
			color: $color_error;
		}
	}
	&:empty {
		padding: 0;
	}
}

.success {
	display: block;
	padding: 2px 0 1px;
	font-size: 11px;
	color: #31a933;
	&:empty {
		padding: 0;
	}
}
