.invoice {
	position: relative;
	&__list {
		position: relative;
	}
	&__item {
		display: block;
		border-bottom: 1px solid #707070;
		padding: 30px 0;
		&:last-child {
			border-bottom: none;
		}
		&:first-child {
			padding: 0 0 20px;
		}
	}
	&__title {
		font-size: 16px;
		padding: 10px 0;
	}
	&__bar {
		position: relative;
		padding: 10px 0;
	}
	&__no {
		float: left;
		color: #27AAE2;
		font-weight: 500;
	}
	&__price {
		float: right;
		font-weight: 700;
	}
	&__action {
		position: relative;
		text-align: center;
		padding: 10px 0 0;
		.button {
			display: inline-block;
			width: 170px;
		}
	}
}
