.kcm {
	position: relative;
	z-index: 9;
	&__superbig {
		text-align: center;
		padding: 30px 0;
	}
	&__middle {
		text-align: center;
		padding: 10px 0;
	}
	&__top {
		text-align: center;
		margin: 0 auto;
	}
	&__stick {
		position: fixed;
		top: 165px;
		z-index: 10;
		width: 100%;
		@include transition(top .2s ease);
		&__wrap {
			position: relative;
			width: 100%;
		}
		&__fluid {
			position: absolute;
			width: calc((100% - 1100px)/2);
			display: block;
			top: 0;
			img {
				max-width: none;
			}
			&--left {
				left: 0;
			}
			&--right {
				right: 0;
			}
		}
		&--sticky {
			top: 150px;
		}
		&--left {
			right: 0;
			position: absolute;
		}
		&--right {
			left: 0;
			position: absolute;
		}
	}
}