.blockquote {
    margin: 50px 0;
    padding-top: 10px;
    
    &__wrapper {
        text-align: center;
        margin-top: 50px;
    }

    &__text {
        font-family: $font_vik;
        letter-spacing: 2px;
        margin: 15px 0;
        padding: 0rem;

        @include min-responsive($screen-sm-min) {
            padding: 0 10rem;

        }

        &--big {
            font-size: 30px;

            @include max-responsive($screen-xs) {
                font-size: 24px;

            }
        }

        &--small {
            font-size: 23px;

            @include max-responsive($screen-xs) {
                font-size: 18px;

            }
        }
    }
}