.tab {
	&__menu {
		position: relative;
		padding: 0 10px;
		margin: 0 -10px;
		white-space: nowrap;
		z-index: 2;
		@include max-responsive($screen-sm-min-1) {
			overflow-y: hidden;
		}
		@include min-responsive($screen-lg-min) {
			padding: 0 25px;
		}
		&__wrap {
			position: relative;
		}
		&__link {
			height: 50px;
			white-space: normal;
			padding: 0 10px;
			@include display-flex();
			align-items: center;
			justify-content: center;
			padding: 0 12px;
			margin: 0;
			text-align: center;
			border-radius: 8px;
			font-weight: 500;
			border-top: 1px solid #C4C4C4;
			border-right: 1px solid #C4C4C4;
			border-left: 1px solid #C4C4C4;
			border-bottom: 1px solid #C4C4C4;
			background: #C4C4C4;
			font-size: 11px;
			@include transition(all .2s ease);
			@include min-responsive($screen-sm-min) {
				border-radius: 8px 8px 0 0;
				border-bottom: 1px solid transparent;
			}
			@include min-responsive($screen-lg-min) {
				border-bottom: 1px solid #707070;
			}
		}
		&__item {
			position: relative;
			display: inline-block;
			width: 90px;
			vertical-align: bottom;
			@include min-responsive($screen-sm-min) {
				width: 75px;
			}
			@include min-responsive($screen-lg-min) {
				width: 100px;
			}
			&:last-child {
				margin-right: 10px;
			}
			&.active .tab__menu__link,
			.tab__menu__link:hover {
				color: #000;
				background: #fff;
				border-top: 1px solid #707070;
				border-left: 1px solid #707070;
				border-right: 1px solid #707070;
				border-bottom: 1px solid #707070;
				@include min-responsive($screen-sm-min) {
					border-bottom: 1px solid transparent;
				}
			}
		}
	}
	&__box {
		position: relative;
		border: 1px solid #707070;
		border-radius: 8px;
		margin: 10px 0 0;
		@include min-responsive($screen-sm-min) {
			margin: 0;
		}
		@include min-responsive($screen-lg-min) {
			top: -1px;
		}
	}
	&__content {
		position: relative;
		padding: 15px;
		background: #fff;
		border-radius: 10px;
		@include min-responsive($screen-sm-min) {
			padding: 20px;
		}
	}
}
