.top {
	&__row {
		position: relative;
		background: #FEEBBE;
		display: none;
		@include min-responsive($screen-sm-min) {
			display: block;
		}
	}
	&__bar {
		position: relative;
		text-align: right;
	}
	&__wrap {
		position: relative;
	}
	&__item {
		display: inline-block;
	}
	&__link {
		display: block;
		padding: 7px 10px;
		color: #000;
		text-transform: uppercase;
		line-height: 1;
		font-size: 11px;
		&:hover {
			color: #000;
		}
	}
}
