.footer {
	position: relative;
	padding: 30px 0;
	text-align: center;
	@include min-responsive($screen-sm-min) {
		border-radius: 50px 50px 0 0px;
		text-align: left;
		// margin: 40px 0 0;
	}
	&__subtitle {
		text-transform: uppercase;
		margin: 5px 0 0;
		font-weight: 300;
		padding: 0 0 10px;
		color: #fff;
		font-size: 16px;
	}
	&__paragraph {
		margin: 5px 0 0;
		padding: 0 0 10px;
		color: #fff;
		line-height: 1.5;
		font-size: 12px;
	}
	&__right {
		@include min-responsive($screen-sm-min) {
			float: right;
		}
	}
	&__social {
		text-align: center;
		padding: 0 0 20px;
		@include min-responsive($screen-sm-min) {
			text-align: left;
			padding: 0 0 25px;
		}
	}
	&__menu {
		color: #fff;
		padding: 0 0 15px;
		margin: 0;
		font-size: 13px;
		font-weight: 300;
		@include min-responsive($screen-sm-min) {
			display: flex;
			flex-direction: column;
			max-height: 220px;
			flex-wrap: wrap;
			padding: 0 0 25px;
		}
	}
	&__item {
		display: inline-block;
		padding: 0;
		font-weight: 500;
		margin: 5px 0;
		&:after {
			content: " -";
		}
		&:last-child {
			&:after {
				content: "";
			}
		}
	}
	&__link {
		&:hover {
			color: #f0f0f0;
		}
	}
	&__copyright {
		padding: 0 25px 20px;
		color: #fff;
		font-size: 13px;
		font-weight: 300;
		@include min-responsive($screen-sm-min) {
			padding: 0 0 10px;
		}
	}
	&__menu--list {
		.footer__item {
			display: block;
		}
		.footer__item:after {
			content: '';
		}
		.footer__link {
			display: inline-block;
			padding: 2px 0;
		}
	}
}

.paylist {
	position: relative;
	padding: 0 0 10px;
	width: 185px;
	display: inline-block;
	margin: 0 auto;
	@include min-responsive($screen-sm-min) {
		padding: 0 0 20px;
	}
	&__item {
		display: inline-block;
		width: 33.333333%;
		float: left;
		padding: 0 5px;
		margin: 0 0 10px;
		@include min-responsive($screen-sm-min) {
			padding: 0;
		}
	}
	&__img {
		img {
			width: 50px;
		}
	}
}
