.breadcrumb{
    margin: 20px 0;
    padding: 0 20px;
    text-align: center;
    @include min-responsive($screen-sm-min){
        margin: 50px 0;
        text-align: left;
        span{
            font-size: 18px;
        }
    }
    span{
        padding-right: 10px;
        margin-right: 10px;
        border-right: 1px solid #000;
        font-size: 14px;
        &:last-child{
            font-weight: 700;
            border: none;
        }
    }
}