.cart {
	&__sticky {
		position: fixed;
		bottom: 1vh;
		left: 0;
		z-index: 9;
		width: 100%;
		padding: 5px;
		@include transition(all .2s cubic-bezier(0.215, 0.610, 0.355, 1.000));
		@include min-responsive($screen-sm-min) {
			left: calc((100% - 500px)/2);
			width: 500px;
		}
		&__box {
			position: relative;
			background: $color_cart;
			border-radius: 10px;
			color: #000;
			font-size: 10px;
			box-shadow: 0 2px 5px rgba(0,0,0,.1);
			padding: 10px 85px 10px 10px;
			@include min-responsive($screen-sm-min) {
				font-size: 12px;
				padding: 10px 140px 10px 10px;
			}
		}
		&__action {
			position: absolute;
			right: 10px;
			bottom: 10px;
			width: 70px;
			@include min-responsive($screen-sm-min) {
				width: 90px;
			}
			.button__default {
				padding: 8px 5px;
				border: 1px solid #fff;
				font-weight: 700;
			}
		}
		&__summary {
			padding: 0 0 5px;
			font-weight: 700;
			font-size: 13px;
		}
		&__list {
			padding: 0 10px;
			span {
				display: block;
				margin: 0 0 2px;
				height: 12px;
				overflow: hidden;
				@include min-responsive($screen-sm-min) {
					height: 15px;
					margin: 0 0 3px;
				}
				&:last-child {
					margin: 0;
				}
			}
		}
		&__close {
			position: absolute;
			top: 2px;
			right: 2px;
			z-index: 1;
			width: 20px;
			height: 20px;
			display: block;
			background: #fff;
			border-radius: 100%;
			padding: 1px;
			border: 1px solid $color_cart;
			@include min-responsive($screen-sm-min) {
				top: -2px;
				right: -2px;
			}
			&:hover {
				box-shadow: 0 2px 2px rgba(0,0,0,.1);
			}
		}
		&.hide {
			visibility: hidden;
			opacity: 0;
			bottom: -10vh;
		}
	}
}
