.login {
	position: relative;
	&__box {
		position: fixed;
		z-index: 10;
		backface-visibility: hidden;
		
		width: 100%;
		height: 100%;
		top: 55px;
		right: 0;
		padding: 20px 15px 70px;
		@include transition(all .2s ease);
		@include min-responsive($screen-sm-min) {
			width: 275px;
			height: auto;
			top: 58px;
			right: calc((100% - 760px)/2);
			padding: 10px 15px 30px;
			box-shadow: 0 2px 5px rgba(0,0,0,.1);
			&:after {
				content: '';
				position: absolute;
				z-index: 1;
				top: -16px;
				right: 35px;
				width: 0;
				height: 0;
				border-left: 8px solid transparent;
				border-right: 8px solid transparent;
				border-top: 8px solid transparent;
				border-bottom: 8px solid #297fc4;
			}
		}
		@include min-responsive($screen-lg-min) {
			right: calc((100% - 1100px)/2);
		}
		@include max-responsive($screen-sm-min-1) {
			overflow-y: scroll;
			white-space: nowrap;
			-webkit-overflow-scrolling: touch;
		}
		.form__submit {
			padding: 0 0 20px;
		}
	}
	&--hide {
		@include max-responsive($screen-sm-min-1) {
			visibility: hidden;
			-ms-transform: translate3d(100%, 0, 0);
			-webkit-transform: translate3d(100%, 0, 0);
			transform: translate3d(100%, 0, 0);
		}
		@include min-responsive($screen-sm-min) {
			visibility: hidden;
			opacity: 0;
			-ms-transform: translate3d(0, -5px, 0);
			-webkit-transform: translate3d(0, -5px, 0);
			transform: translate3d(0, -5px, 0);
		}
	}
	&__wrap {
		position: relative;
		color: #fff;
		a {
			color: #fff;
			//padding: 0 5px;
		}
	}
	&__title {
		text-align: center;
		font-size: 18px;
		text-transform: uppercase;
		padding: 20px 0;
		font-weight: 300;
	}
	&__header {
		padding: 0 0 20px;
		text-align: center;
	}
	&__footer {
		text-align: center;
	}
	&--full {
        padding: 10vh 0;
        min-height: 58.3vh;
        @include max-responsive($screen-xl){
            min-height: 54.5vh;
        }
        @include max-responsive($screen-md){
            min-height: 100vh;
        }
		.login__title {
			font-size: 20px;
		}
		.form__submit {
			padding: 0 0 20px;
		}
	}
}
