.profile {
	position: relative;
	&__header {
		position: relative;
		text-align: center;
		padding: 0 0 20px;
	}
	&__img {
		width: 80px;
		height: 80px;
		margin: 0 auto 10px;
		border-radius: 100%;
		border: 1px solid #000;
		overflow: hidden;
		@include min-responsive($screen-sm-min) {
			width: 100px;
			height: 100px;
		}
	}
	&__name {
		font-weight: 700;
		font-size: 16px;
		padding: 0;
	}
	&__info {
		font-size: 12px;
		font-weight: 500;
		color: #C4C4C4;
	}
}
