.category {
    margin: 15px 0 50px;

    &__wrapper {
        @include min-responsive($screen-md) {
            display: flex;
            align-items: flex-end;
		}
    }

    &__title {
        flex: 0 0 25%;
        padding: 20px 10px;
        border-bottom: 1px solid $color_border;
        text-align: center;
        @include min-responsive($screen-sm-min) {
            text-align: right;
        }
    }

    &__list {
        margin-top: 15px;
        @include min-responsive($screen-sm-min) {
            flex: 0 0 75%;
            display: flex;
		}
    }

    &__item {
        position: relative;
        width: 110px;
        margin-right: 10px;
        &:hover{
            img{
                transform: scale(1.2)
            }
        }
        @include min-responsive($screen-sm-min) {
            margin: 15px 10px;
            width: 20%;
		}
    }
    &__img{
        position: relative;
        overflow: hidden;
        border-radius: 30px;
        height: 244px;
        @include max-responsive($screen-sm-min){
            height: 220px;
        }
        img {
            border-radius: 30px;
            transition: 1s ease-in-out;
            transform: scale(1);
            height: 100%;
            object-fit: cover;
        }
    }
    &__box {
        position: absolute;
        left: 0px;
        bottom: 0;
        width: 100%;
        padding: 20px 0;
        background: rgba(0, 0, 0, .75);
        border-radius: 0px 0px 30px 30px;
        text-align: center;
        height: 90px;
        @include max-responsive($screen-sm-min){
            height: 70px;
            padding: 10px 0;
        }
        &Title{
            color: #fff;
            text-transform: uppercase;
            padding: 0 10px;
            margin: 5px 0;
            line-height: 1.5;
            max-height: 42px;
            overflow: hidden;
            font-size: 14px;
            @include max-responsive($screen-sm-min){
                padding: 0 10px;
                font-size: 12px;
            }
        }
    }
}