.social {
	position: relative;
	&__item {
		display: inline-block;
		margin: 0 2px;
	}
	&__link {
		display: block;
		width: 30px;
		height: 30px;
		background: transparent;
		border-radius: 100%;
		@include transition(all .2s ease);
		&:hover {
			background: $color_error;
		}
	}
}
