.btn {
    &Submit {
        background: #fff;
            border-radius: 25px;
            box-shadow: 0 2px 20px 0 rgba(255, 255, 255, 0.25);
            color: $color_submit;
            display: inline-block;
            padding: 5px 10px;
            border-radius: 100px;     
            margin: 0 auto;
            &__link {
                display: flex;
                justify-content: center;
                align-items: center;
                >span{
                    padding: 0px;
                    transition: .2s ease-in-out;
                @include min-responsive($screen-sm-min) {
                    padding: 0 20px;
                }
                @include max-responsive($screen-sm-min) {
                    font-size: 12px;
                }
            }
            &:hover{
                >span{
                    @include min-responsive($screen-sm-min) {
                        padding: 0 25px;
                    }
                }
            }
        }

        &__bg {
            width: 30px;
            height: 30px;
            @include linier_gradient_2($color_gradl, $color_gradd, 180deg);
            border-radius: 50px;
            margin-left: 10px;
            @include min-responsive($screen-sm-min) {
                width: 40px;
            height: 40px;
            }
        }

        &__icon {
            display: inline-block;
            width: 30px;
            height: 30px;
            background-repeat: no-repeat;
            background-position: center;
            @include min-responsive($screen-sm-min) {
                width: 40px;
                height: 40px;
            }
        }
    }
    &More{
        &__link{
            display: inline-block;
            padding: 5px 10px;
            border-bottom: 1px solid #000;
            font-family: $font_vik;
            letter-spacing: 2px;
            font-size: 16px;
            transition: 3s ease-in-out;
            &:hover{
                padding: 5px 15px;
            }
        }
    }
}

.button {
	font-size: 10px;
	padding: 12px 5px;
	text-transform: uppercase;
	font-weight: 500;
	display: block;
    text-align: center;
	@include transition(all .2s ease);
	&:hover,
	&:focus {
		color: inherit;
	}
	&__back {
		position: relative;
		display: block;
		width: 40px;
		height: 40px;
		.icon {
			background-size: 40%;
		}
	}
	&__default {
		color: #2a2a2a;
		background: #fff;
		border: 1px solid #2a2a2a;
		&:hover,
		&:focus {
			color: #000;
			border: 1px solid #2a2a2a;
			background: lighten(#2a2a2a, 75%);
		}
	}
	&__primary {
		color: #fff;
		background: $color_error;
		border: 1px solid $color_error;
		&:hover,
		&:focus {
			color: #fff;
			border: 1px solid $color_theme;
			background: $color_theme;
		}
	}
	&__secondary {
		color: #000;
		border: 1px solid #707070;
		&:hover,
		&:focus {
			color: #000;
			border: 1px solid #707070;
		}
	}
	&__login,
	&__tertiary {
		color: #fff;
		background: $color_error;
		border: 1px solid $color_error;
		font-size: 14px;
		padding: 10px 5px;
		&:hover,
		&:focus {
			color: #fff;
			background: darken($color_error, 5%);
			border: 1px solid darken($color_error, 5%);
		}
	}
	&__tertiary {
		font-size: 12px;
	}
	&:disabled {
		border: 1px solid darken(#f4f4f4, 15%);
		background: darken(#f4f4f4, 10%);
		color: darken(#f4f4f4, 40%);
	}
	.icon {
		width: 20px;
		height: 20px;
		margin-right: 2px;
		display: inline-block;
		vertical-align: bottom;
		background-size: 75%;
	}
}
