.address {
	&__box {
		padding: 10px;
		border-radius: 10px;
		border: 1px solid #707070;
		box-shadow: 0 2px 5px rgba(0,0,0,.1);
		margin: 0 0 40px;
		@include min-responsive($screen-sm-min) {
			margin: 0 0 20px;
			padding: 20px 20px;
		}
	}
	&__header {
		font-weight: 500;
		padding: 15px 15px;
		background: #F6F6F6;
	}
	&__list {
		padding: 0;
	}
	&__item {
		position: relative;
	}
	&__status {
		position: absolute;
		right: 0;
		top: 5px;
		z-index: 1;
		font-size: 11px;
		font-weight: 500;
		line-height: 1;
		color: $color_theme;
		background: #fff;
		padding: 3px 6px;
	}
	&__info {
		position: relative;
		font-size: 12px;
		line-height: 1.5;
		&__name {
			font-weight: 500;
			font-size: 16px;
			padding: 2px 0;
		}
		&__phone {
			padding: 5px 0;
		}
		&__address {
			padding: 1px 0;
		}
	}
	&__radio {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
	}
	&__footer {
		padding: 15px 15px;
	}
	&__add {
		display: inline-block;
		padding: 8px;
		font-weight: 500;
		font-size: 12px;
		text-transform: uppercase;
		border: 1px solid #707070;
	}
	&__template {
		position: relative;
	}
	&__form {
		&__footer {
			padding: 10px 0;
			text-align: center;
		}
	}
	&__button {
		display: inline-block;
		font-size: 11px;
		text-transform: uppercase;
		color: #000;
		border-radius: 0;
		padding: 5px 20px;
		font-weight: 500;
	}
}