.share {
	position: relative;
	&__item {
		display: inline-block;
        margin: 0 2px;
        border-radius: 30px;
        &--fb{
            background: $color_facebook;
        }
        &--twitter{
            background: $color_twitter;
        }
        &--instagram{
            background: $color_instagram;
        }
	}
	&__link {
		display: flex;
		background: transparent;
        border-radius: 100%;
        align-items: center;
        padding-right: 10px;
        color: #fff;
        font-weight: 700;
        @include transition(all .2s ease);
        &:hover{
            color: #fff;
        }
        .icon{
            width: 30px;
		    height: 30px;
        }
	}
}
