.payment {
	&__box {
		padding: 10px;
		border-radius: 10px;
		border: 1px solid #707070;
		box-shadow: 0 2px 5px rgba(0,0,0,.1);
		margin: 0 0 40px;
		@include min-responsive($screen-sm-min) {
			padding: 20px 20px;
			margin: 0;
		}
	}
	&__title {
		font-weight: 500;
		padding: 15px 0;
	}
	&__list {
		position: relative;
		margin: 0;
	}
	&__item {
		position: relative;
		padding: 0 10px;
		border-bottom: 1px solid #efefef;
		&:last-child {
			border-bottom: 0px;
		}
		label {
			display: block;
			padding: 15px 0 15px 25px;
		}
	}
	&__radio {
		position: absolute;
		z-index: 1;
		top: 15px;
		left: 10px;
	}
	&__info {
		font-size: 13px;
		@include min-responsive($screen-sm-min) {
			font-size: 14px;
		}
	}
	&__detail {
		position: relative;
		padding: 0 25px;
		//font-size: 13px;
		line-height: 1.5;
		a {
			text-decoration: underline;
		}
	}
	&__detail {
		display: none;
//		&.show {
//			display: block;
//		}
	}
	&__status {
		position: relative;
		padding: 20px;
		border-radius: 8px;
		text-align: center;
		margin: 0;
		background: #F6F6F6;
		&__title {
			padding: 20px 0 10px;
			font-weight: 700;
			font-size: 15px;
			text-transform: uppercase;
		}
		&__lead {
			font-size: 13px;
		}
		&__img {
			img {
				height: 75px;
			}
		}
		&__box {
			padding: 10px;
			border-radius: 10px;
			border: 1px solid #707070;
			box-shadow: 0 2px 5px rgba(0,0,0,.1);
			margin: 0 0 40px;
			@include min-responsive($screen-sm-min) {
				width: 500px;
				padding: 20px 50px;
				margin: 0 auto;
			}
		}
	}
}
