.dash {
	position: relative;
	&__menu {
		position: relative;
		padding: 0;
		margin: 0 -10px 20px;
        background: #fff;
        border: 2px solid $color_border;
		@include max-responsive($screen-sm-min-1) {
			display: none;
			overflow-y: hidden;
			white-space: nowrap;
			box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 6px 0px;
		}
		@include min-responsive($screen-sm-min) {
			margin: 20px 0;
			padding: 20px;
			border-radius: 10px;
			//border: 1px solid #707070;
			box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
		}
		&__wrap {
			position: relative;
		}
		&__link {
			padding: 15px 2px 10px;
			margin: 0 8px;
			display: block;
			font-weight: 300;
			text-transform: uppercase;
			font-size: 13px;
			border-radius: 10px;
			position: relative;
			@include min-responsive($screen-sm-min) {
				padding: 15px 10px;
				margin: 0;
			}
			&:hover {
				//background: #fff;
				color: #2a2a2a;
			}
		}
		&__item {
			position: relative;
			display: inline-block;
			@include min-responsive($screen-sm-min) {
				display: block;
			}
			&.active {
				.dash__menu__link {
					font-weight: 500;
					color: #000;
				}
				&:after {
					content: '';
					position: absolute;
					bottom: 1px;
					z-index: 1;
					width: 100%;
					left: 0%;
					border-bottom: 3px solid $color_theme;
					@include min-responsive($screen-sm-min) {
						width: 92%;
						left: 4%;
					}
				}
			}
			.badge {
				position: absolute;
				right: 10px;
				top: 14px;
			}
		}
	}
	&__box {
		padding: 10px 0 20px;
		margin: 0;
		border-radius: 10px;
		@include min-responsive($screen-sm-min) {
			margin: 20px 0;
		}
	}
	&__form {
		padding: 10px;
		margin: 0 0 20px;
		border-radius: 10px;
		border: 1px solid #707070;
        box-shadow: 0 2px 5px rgba(0,0,0,.1);
        background: #fff;
		border-radius: 10px;
		@include min-responsive($screen-sm-min) {
			margin: 0 0 40px;
			padding: 20px;
		}
	}
}
