.shipping {
	&__box {
		padding: 10px;
		border-radius: 10px;
		border: 1px solid #707070;
		box-shadow: 0 2px 5px rgba(0,0,0,.1);
		margin: 0 0 40px;
		@include min-responsive($screen-sm-min) {
			padding: 20px 20px;
			margin-bottom: 20px;
		}
	}
	&__title {
		padding: 5px 0 10px;
		font-size: 13px;
		font-weight: 500;
	}
	&__item {
		position: relative;
		font-size: 13px;
		//padding: 0 50px 0 0;
		label {
			display: block;
			padding: 10px 0;
		}
	}
	&__radio {
		position: relative;
		display: block;
		font-size: 13px;
		@include min-responsive($screen-sm-min) {
			font-size: 14px;
		}
		input[type='radio'] {
			margin-right: 5px;
			@include min-responsive($screen-sm-min) {
				margin-right: 15px;
			}
		}
		span {
			padding: 0 5px;
			font-size: 13px;
			display: inline-block;
			vertical-align: text-bottom;
			@include min-responsive($screen-sm-min) {
				padding: 0 15px;
			}
		}
	}
	&__price {
		vertical-align: top;
		display: inline-block;
		float: right;
	}
}