.thumb {
    position: relative;
    margin: 20px -5px;

    &__list {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        flex: 0 0 50%;
        padding: 0 5px;
        position: relative;
        border-radius: 30px;
        text-align: center;
        margin-bottom: 15px;
        @include max-responsive($screen-sm-min) {
            margin-bottom: 30px;
            width: 50%;
        }
    }

    &__box {
        position: absolute;
        left: 5px;
        bottom: 0;
        width: calc(100% - 10px);
        padding: 20px 0;
        background: rgba(0, 0, 0, .5);
        border-radius: 0px 0px 40px 40px;
        @include max-responsive($screen-sm-min) {
            padding: 0;
            border-radius: 0px 0px 10px 10px;
        }
    }

    &__content {
        display: flex;
        align-items: center;
        justify-content: center;

        @include max-responsive($screen-sm-min) {
            flex-direction: column
        }
    }

    &__title {
        flex: 0 0 50%;
        font-family: $font_title;
        letter-spacing: 5px;
        font-size: 34px;
        color: white;

        @include max-responsive($screen-sm-min) {
            font-size: 23px;
            transform: translateY(10px);
        }
    }

    &__submit {
        display: inline-flex;

        @include max-responsive($screen-sm-min) {
            transform: translateY(15px);
            box-shadow: 0 2px 20px 0 rgba(87, 87, 87, .55);
        }
    }

    &__img {
        img {
            width: 100%;
        }
    }
}