// reset form
label {
	cursor: pointer;
}

input,
select,
button,
textarea,
.button {
	margin: 0;
	padding: 8px 12px;
	border-radius: 8px;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.05);
	background-color: #fff;
	border: solid 1px #C4C4C4;
	outline: 0;
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	@include transition(all .2s ease);
	@include font-pack(false, 12px, 400, 1.42857143, #000);

	&::placeholder {
		font-size: 12px;
		color: #C2BEBE;
		vertical-align: middle;
	}

	&:focus {
		color: #000;
		border: 1px solid #ddd;
		box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.18);
	}
}

button,
select,
input[type="submit"],
input[type="text"],
input[type="password"] {
	//height: 40px;
}

input[type="text"],
input[type="password"] {
	&:disabled {
		background: #f4f4f4;

		&:hover,
		&:focus {
			color: #C2BEBE;
			border: solid 1px #C4C4C4;
			box-shadow: inherit;
		}
	}
}

select {
	@include min-responsive($screen-sm-min) {
		font-size: 12px;
		color: #C2BEBE;
		vertical-align: middle;
		padding: 10px 12px 11px;
	}
}

textarea {
	min-height: 100px;
	max-width: 100%;
	display: block;
	width: 100%;
}

button,
input[type="checkbox"],
input[type="radio"],
input[type="button"],
input[type="submit"],
input[type="reset"] {
	cursor: pointer;
}

input[type="radio"] {
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	padding: 0;
	border: none;
	line-height: 1;
	box-shadow: none;
	position: relative;

	&:before {
		content: '';
		background: #f4f4f4;
		border-radius: 100%;
		border: 1px solid darken(#f4f4f4, 25%);
		display: inline-block;
		width: 20px;
		height: 20px;
		position: relative;
		top: 0px;
		margin-right: 0;
		vertical-align: top;
		cursor: pointer;
		text-align: center;
		@include transition(all .2s ease);
	}

	&:checked {
		&:before {
			border: 1px solid darken(#f4f4f4, 70%);
			background: #5B4CAE;
			box-shadow: inset 0 0 0 2.5px #fff;
		}
	}

	&:disabled {
		&:before {
			border: 1px solid darken(#f4f4f4, 10%);
			background: #f4f4f4;
		}
	}

	&:disabled:checked {
		&:before {
			border: 1px solid darken(#f4f4f4, 10%);
			background: darken(#f4f4f4, 10%);
			box-shadow: inset 0 0 0 2.5px #fff;
		}
	}
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

input[type="file"] {
	padding: 8px 0;
	border: none;
	box-shadow: none;
}

// style form
.form {
	&__attribute {
		padding: 12px 0 0;
	}

	&__group {
		position: relative;
		display: block;
		margin: 0 0 10px;

		input,
		select,
		button,
		textarea {
			display: block;
			width: 100%;
		}

		&.select {
			position: relative;

			select {
				background: transparent;
			}

			&:after {
				content: '';
				position: absolute;
				top: 15px;
				right: 15px;
				width: 10px;
				height: 10px;
				border-left: 2px solid #ccc;
				border-bottom: 2px solid #ccc;
				transform: rotate(-45deg);
				z-index: -1;
			}

			option {
				text-transform: capitalize;
			}
		}
	}

	&__radio {
		padding: 13px 0 9px;

		input[type="radio"] {
			display: inline-block;
			width: auto;
			margin-right: 10px;
		}

		label {
			display: block;
		}
	}

	&__select {
		position: relative;
		background: transparent;

		&:hover,
		&:focus {
			color: #000;
		}

		&:after {
			content: '';
			position: absolute;
			right: 0;
			top: 0;
			width: 10px;
			height: 10px;
			border-bottom: 2px solid #000;
			border-right: 2px solid #000;
		}
	}

	&__footer {
		padding: 10px 0;
		position: relative;
	}

	&__submit {
		margin-bottom: 25px;

		.form__group {
			margin-bottom: 0;
		}

		&>div {
			padding-left: 5px;
			padding-right: 5px;
		}
	}

	&--hide {
		visibility: hidden;
		display: none;
	}

	&__wrapper {
		background: #fff;
		margin: 20px 0;
		padding: 20px;
		border-radius: 10px;
		border: 2px solid $color_border;

		@include max-responsive($screen-sm-min-1) {
			// display: none;
			overflow-y: hidden;
			white-space: nowrap;
			box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 6px 0px;
		}
	}
}