.title {
	position: relative;
	text-align: center;
	color: #070707;
	font-size: 24px;
	font-weight: 300;
	font-family: $font_title;
	letter-spacing: 5px;
	margin-bottom: 20px;
	
	@include min-responsive($screen-sm-min) {
		font-size: 30px;
	}
	
	&--big {
		letter-spacing: 2px;
		text-transform: none;
		margin-bottom: 20px;
		font-size: 30px;
		@include min-responsive($screen-sm-min) {
			font-size: 40px;
		}
	}
	&--small {
		font-size: 20px;
		letter-spacing: 2px;
		text-transform: none;
		margin-bottom: 20px;
		@include min-responsive($screen-sm-min) {
			font-size: 26px;
		}
	}

	&__bar {
		position: relative;
		margin: 0 -10px;
		padding: 12px 40px 12px 40px;
		background: #E9E9E9;

		@include min-responsive($screen-sm-min) {
			margin: 0;
			background: transparent;
			padding: 12px 15px 12px 15px;
		}
	}
	&__border{
		padding: 20px 20px 0px 20px;
		border-top: 1px solid $color_border;
	}
	&__button {
		position: absolute;
		z-index: 1;
		left: 0;
		top: 5px;
		box-shadow: none;
		border: none;
		padding: 0;
		background-color: inherit;

		@include min-responsive($screen-sm-min) {
			display: none;
		}
	}

	&__head {
		padding: 5px 0 15px;
		font-size: 20px;
		font-family: $font_vik;
		@include min-responsive($screen-sm-min) {
			padding: 0 0 20px;
			font-size: 24px;
		}
	}

	&__related {
		position: relative;
		background: #C4C4C4;
		color: #fff;
		text-transform: uppercase;
		font-size: 16px;
		padding: 15px 10px;
		margin: 0 -10px;

		@include min-responsive($screen-sm-min) {
			padding: 10px 20px;
			margin: 0;
		}
	}

	&__bar {
		&.home {
			padding: 12px 30px 12px 30px;

			//background: transparent;
			@include min-responsive($screen-sm-min) {
				padding: 12px 20px 12px 20px;
			}
		}

		&.home .title {
			text-align: left;
		}

		&.home .title:after {
			content: '';
			position: absolute;
			left: -15px;
			top: 10px;
			width: 7px;
			height: 7px;
			background: #EC2628;
			z-index: 1;
		}
	}
	&--white{
		color: #fff;
	}
	&--grey{
		color: $color_grey;
	}
	&--static{
		text-align: left;
	}
}